* {
  margin: 0px;
  box-sizing: border-box;
}
html, body {
  width: 100%;
  height: 100%;
}
body {
  background: #F8EEE7;
}
#root {
  width: 100%;
  height: 100%;
}
#logo{
  position: absolute;
  top: 18px;
  left: 20px;

}
/* Our App Container */
.container {
  padding-bottom: 80px;
}
/* The header */
.header {
  background: #94618E;
  padding: 50px;
}
/* The note */
.note {
  color: #FFFFFF;
  padding: 40px;
  margin: 20px;
  background: #49274A;
  border: solid 1px #e9e9e9;
  border-radius: 15px;
  cursor: pointer;
  box-shadow: 0px 0px 14px 0px rgba(0,0,0,0.1);
  -webkit-box-shadow: 0px 0px 14px 0px rgba(0,0,0,0.1);
  -moz-box-shadow: 0px 0px 14px 0px rgba(0,0,0,0.1);
  transition: transform .2s ease;
}
.note:hover {
  transform: scale(1.02);
}
/* The add note button */
.button {
  position: fixed;
  z-index: 1;
  right: 40px;
  bottom: 40px;
  width: 80px;
  height: 80px;
  border-radius: 100%;
  text-align: center;
  line-height: 65px;
  font-size: 50px;
  font-weight: bold;
  color: #49274A;
  background: #F4DECB;
  cursor: pointer;
  box-shadow: 0px 0px 11px 0px rgba(0,0,0,0.8);
  -webkit-box-shadow: 0px 0px 11px 0px rgba(0,0,0,0.8);
  -moz-box-shadow: 0px 0px 11px 0px rgba(0,0,0,0.8);
  transition: transform .06s ease;
}
.button:hover {
  transform: scale(1.2);
}
/* Textinput */
.textInput {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 80px;
  line-height: 80px;
  padding: 0 20px;
  border: none;
  outline: none;
  background: #49274A;
  font-size: 16px;
  color: #F8EEE7;
}
